nav {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    background-color: rgb(228, 146, 39);
    gap: 45px;
}

nav a{
    text-decoration: none;
    color: white;
}

nav a:hover {
    color: cadetblue;
}

nav a svg{
    margin-right: 5px;
}