.card {
    box-shadow: 2px 8px 21px -3px #777777;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s;
}

.card:hover {
    background-color: #f1f0f0;
}
.card.active {
    background-color: #f1f0f0;
}

.cardName {
    padding: 0;
    font-size: 24px;
    margin-bottom: 10px;
}